.ReactTable {
    border: $not-apply;
    position: relative;
    flex-grow: 1;
    max-height: 100%;
  
    .rt-table {
      .rt-thead{
        .rt-tr {
          height: 40px;
          display: flex;
          .rt-th {
            @include font($font-family-medium, $body2-font-size, $black-color, false, normal);
            display: flex;
            align-items: center;
            padding: 0 10px;
            width: 100%;
            &:first-child {
              border-right: 0;
              padding: 0 10px 0 18px;
            }
          }
        }
      }
      .rt-tbody {
        .rt-tr-group {
          .rt-tr {
            align-items: center;
            background: $white-color;
            position: relative;
            min-height: 40px;
            display: flex;
            margin-bottom: 5px;
            .rt-td {
              @include font(
                $font-family,
                $body2-font-size,
                $gray-color-active,
                false,
                normal
              );
              text-align: left;
              border-right: 0;
              padding: 0 10px;
              width: 100%;
              &:first-child {
                padding: 0 10px 0 18px;
              }
            }
          }
        }
      }
    }
    .pagination-bottom {
      @include font($font-family, $body-font-size, $gray-color);
      font-weight: 400;
      background: $white-color;
      z-index: 0;
      .-pagination {
        padding: 10px 20px;
        .-btn {
          background: $white-color !important;
  
          &:hover {
            background: $white-color !important;
            color: $link !important;
          }
          &.back-button {
            @include down-arrow($gray-color, 4px);
            padding: 0 6px;
            &:after {
              margin: 0;
              transform: rotate(90deg);
            }
          }
          &.next-button {
            @include down-arrow($gray-color, 4px);
            padding: 0 6px;
            &:after {
              margin: 0;
              transform: rotate(-90deg);
            }
          }
        }
        .dark-gray-text {
          color: #37324d;
        }
        .right-section {
          label,
          ul {
            margin: 0;
          }
          .pr-3 {
            display: flex;
            font-size: $body2-font-size;
            .label {
              flex: 1;
              span {
                position: relative;
                top: 7px;
              }
            }
            .dropdown {
              .dropdown-menu {
                width: 60px;
                height: 60px;
                flex-direction: column;
              }
            }
          }
          .dropup {
            .dropdown-toggle {
              padding: 8px 18px;
            }
          }
          .pagination {
            li {
              .page-link {
                @include font($font-family, $body2-font-size, $gray-color);
                font-weight: 400;
                border: none;
                margin: 0;
                padding: 0;
                padding-right: 18px;
                &:hover {
                  background-color: white;
                }
                &:focus {
                  outline: none;
                  box-shadow: none;
                }
              }
            }
            li.page-item.active {
              span.page-link {
                @include font($font-family, $body-font-size, $gray-color-active);
                font-weight:bolder;
                background-color: white;
              }
            }
            li.page-item.disabled {
              span {
                span {
                  color: $secondary-disable-color;
                }
              }
            }
          }
        }
      }
    }
  }
  
  .spog-datagrid-paper {
    height: 100%;
    width: 100%;
    // padding-bottom: 20px;
    .spog-datagrid-container {
      height: calc(100% - 60px);
      width: 100%;
    }
    &.no-pagination{
      height: 100%;
      .spog-datagrid-container {
        height: 100%;
      }
    }
  }
  
  ///data grid wrapper
  .spog-data-grid-wrapper {
    .ReactTable {
        .rt-tbody {
          max-height: 180px;
        }
      }
    position: relative;
    height: 100%;
    overflow: hidden;
  }


  .spog-data-grid-wrapper-mui {
    position: relative;
    height: 100%;
    width: 100%;
    overflow: hidden;
  }
  
  
  .data-grid-heading {
    @include font($font-family, $h2-font-size, var(--primary-text-color));
    font-weight: 400;
    padding: 20px 18px;
    // background: var(--page-bg-color);
    border-top: 1px solid $dark-gray-border;
  }
  
  ///context menu in data grid
  .react-contextmenu {
    min-width: 160px;
    padding: 5px 0;
    margin: 2px 0 0;
    font-size: $body-font-size2;
    color: #373a3c;
    text-align: left;
    background-color: $white-color;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-radius: 0.25rem;
    outline: none;
    pointer-events: auto !important;
  }

  .resizer {
    display: inline-block;
    border-right: 1px solid #e8e8e8;
    width: 16px;
    height: 50%;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(0%, -50%);
    z-index: 1;
    touch-action:none;
  }
  
