  
  @font-face {
    font-family: "Sans Source Pro Regular";
    src: url("#{$font-path}/SourceSansPro-Regular.eot");
    src: url("#{$font-path}/SourceSansPro-Regular.ttf") format("truetype"),
        url("#{$font-path}/SourceSansPro-Regular.eot?#iefix") format('embedded-opentype');
  }
  
  @font-face {
    font-family: "Sans Source Pro Medium";
    src: url("#{$font-path}/SourceSansPro-SemiBold.eot");
    src: url("#{$font-path}/SourceSansPro-SemiBold.ttf") format("truetype"),
        url("#{$font-path}/SourceSansPro-SemiBold.eot?#iefix") format('embedded-opentype');
  }
  
  @font-face {
    font-family: "Sans Source Pro Bold";
    src: url("#{$font-path}/SourceSansPro-Bold.eot");
    src: url("#{$font-path}/SourceSansPro-Bold.ttf") format("truetype"),
        url("#{$font-path}/SourceSansPro-Bold.eot?#iefix") format('embedded-opentype');
  }
  @font-face {
    font-family: "Sans Source Pro Light";
    src: url("#{$font-path}/SourceSansPro-Light.ttf") format("truetype"),
    url("#{$font-path}/SourceSansPro-Regular.ttf") format("truetype");
  }

  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  body,
html,
#root {
  @include viewport($full-width, $full-height);
  color-scheme: var(--scrollbar-color);
}
  .d-flex {
    display: flex;
  }

  .justify-content-center{
    justify-content: center;
  }

  .align-items-center{
    align-items: center;
  }

  .spog-app-wrapper {
    height: 100%;
    padding: 0px 0px 0px 0;
    .content-wrapper {
      display: flex;
      position: relative;
      flex-grow: 1;
      width: calc(100% - 70px - 220px);
      height: 100%;
      flex-direction: column;
      .app-banner {
        @include font($font-family, $h3-font-size, $gray-color-active);
        font-weight: 400;
        width: 100%;
        position: absolute;
        background: #fffcb4;
        border-bottom: 1px solid $dark-gray-border;
        padding: 5px 0;
        text-align: center;
        z-index: +100;
      }
      .app-content {
        display: flex;
        width: 100%;
        position: relative;
        height: calc(100% - 135px);
        &.impersonation-view {
          //height: calc(100% - 160px);
          padding-top: 0px;
        }
        .app-render {
          flex-grow: 1;
          width: calc(100% - 70px - 220px);
          height: $full-height;
          > .nav {
            border-top-right-radius: 2px;
          }
          .load-view-content {
            height: $full-height;
            background: var(--gray-bg);
            // overflow-y: auto;
          }
          .load-view-content.hide-scroll {
            -ms-overflow-style: none;  /* Internet Explorer 10+ */
            scrollbar-width: none;  /* Firefox */
          }
          .load-view-content.hide-scroll::-webkit-scrollbar {
            display: none;  /* Safari and Chrome */
          }
        }
      }
    }
  }
  .spog-app-wrapper {
    height: calc(100% - 64px);
    padding-top: 64px;
   
  }
  .spog-data-grid-wrapper {
    position: relative;
    height: 100%;
    overflow: hidden;
  }

  .async-wrapper {
    position: relative;
    // margin-top:30px;
    @include viewport(100%, 100%);
  }
 
  .spog-datagrid-container{
    table{
      thead {
        tr{
          th{
        //  border-bottom:none;
        //  height: 40px;
        //  margin:0;
        display: flex !important;
        align-items: center;
          }
        }
      }
  
    }
  }
