/*
*CUSTOM MIXINS
*/

///to apply font properties
@mixin font($fontfamily, $size: false, $color: false, $lh: false, $weight: false) {
    font-family: $fontfamily;
    @if $size {
        font-size: $size;
    }
    @if $color {
        color: $color;
    }
    @if $weight {
        font-weight: $weight;
    }
    @if $lh {
        line-height: $lh;
    }
}

///to add link with all state
@mixin link {
    color: $link;
    font-family: $font-family;
    font-weight: 550;
    text-decoration: none;
    &:active {
        color: $link-hover;
    }
    &:visited {
        color: $link;
    }
    &:hover {
        color: $link-hover;
    }
    &.disabled {
        color: gray;
        pointer-events: none;
    }
}

///to apply any width and height
@mixin viewport($width, $height) {
    width: $width;
    height: $height;
}

///to apply zero padding and margin
@mixin nospacing {
    margin: $not-apply;
    padding: $not-apply;
}

///to apply any margin with postion
@mixin margin($margin, $pos: false) {
    margin: $margin;
    @if $pos {
        position: $pos
    }
    ;
}

///to add ellipsis to any element
@mixin truncate($truncation-width) {
    max-width: $truncation-width;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

///to apply border radius
@mixin border-radius($radius) {
    -webkit-border-radius: $radius;
    -moz-border-radius: $radius;
    -ms-border-radius: $radius;
    border-radius: $radius;
}

///to add arrow with any color and width
@mixin down-arrow($color, $border-width) {
    &:after {
        display: inline-block;
        width: 0;
        height: 0;
        margin-left: 15px;
        vertical-align: middle;
        content: "";
        border-top: $border-width solid $color;
        border-right: $border-width solid transparent;
        border-bottom: 0;
        border-left: $border-width solid transparent;
    }
}
///to add arrow with any color and width
@mixin right-arrow($color, $border-width) {
    &:after {
        display: inline-block;
        width: 0;
        height: 0;
        margin-left: 15px;
        vertical-align: middle;
        content: "";
        border-top: $border-width solid transparent;
        border-right: 0;
        border-bottom: $border-width solid transparent;
        border-left: $border-width solid $color;
    }
}

/// Button sizes
@mixin button-size($padding-y, $padding-x, $font-size, $letter-spacing) {
    padding: $padding-y $padding-x;
    font-size: $font-size;
    letter-spacing: $letter-spacing;
}

///to add transition effect with any speed
@mixin fadein($speed) {
    -webkit-transition: all $speed ease;
    -moz-transition: all $speed ease;
    -o-transition: all $speed ease;
    -ms-transition: all $speed ease;
    transition: all $speed ease;
}


